                    <div class="block">
                        <el-col :span="23" :offset="1">

                            <!-- 原子选择器 -->
                            <el-col :span="5" class="analysis-atom-select">
                                {{i18n.atom}}
                                <el-select filterable :placeholder="i18n.choice" @change="dataTableDataByTemplate()" v-model="atom">
