                            </el-col>
                            <!-- 原子选择器end -->

                            <!-- 时间选择器 -->
                            <el-col :span="8" class="analysis-date-picker">
                                {{i18n.startAndEndTime1}}
                                <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByTemplate()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                </el-date-picker>
                            </el-col>
                            <!-- 时间选择器end -->

                            <!-- 业务选择器 -->
                            <el-col :span="5" class="analysis-select">
                                {{i18n.businessName}}
                                <el-select filterable :placeholder="i18n.choice" @change="dataTableDataByTemplate()" v-model="biz_cc_id">
                                    <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </el-col>
                            <!-- 业务选择器end -->

                            <!-- 分组选择器 -->
                            <el-col :span="5" class="analysis-select">
                                {{i18n.category}}
                                <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByTemplate()" v-model="category">
                                    <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                            <!-- 分组选择器end -->
                        </el-col>
                    </div>
                    <!-- 表格 -->
                    <el-col :span="22" :offset="1" class="analysis-dataTable">
                        <data-table-pagination
                        :data="templateData"
                        :total="templateTotal"
                        :otherHeight="otherHeight"
                        :pagination="templatePagination"
                        :options="dataTableOptions"
                        :columns="templateColumns"
                        :operates="templateOperates"
                        @handleSizeChange="templateHandleSizeChange"
                        @handleIndexChange="templateHandleIndexChange">
                        </data-table-pagination>
                    </el-col>
                    <!-- 表格end -->
                </div>
                </el-col>
            </bk-tabpanel>
            <!-- 流程引用选项卡 end -->

            <!-- 执行选项卡 -->
            <bk-tabpanel name="execute" :title="i18n.execute">
                <div class="p20">{{i18n.executeName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime2}}
                                    <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByExecute()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByExecute()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->

                                <!-- 分组选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.category}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByExecute()" v-model="category">
                                        <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                 <!-- 分组选择器end -->
                            </el-col>
                        </div>
                        <!-- 表格 -->
                        <el-col :span="22" :offset="1" class="analysis-dataTable">
                            <data-table-pagination
                            :data="executeData"
                            :total="executeTotal"
                            :otherHeight="otherHeight"
                            :pagination="executePagination"
                            :options="dataTableOptions"
                            :columns="executeColumns"
                            :operates="executeOperates"
                            @handleSizeChange="executeHandleSizeChange"
                            @handleIndexChange="executeHandleIndexChange">
                            </data-table-pagination>
                        </el-col>
                        <!-- 表格end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 执行选项卡end -->

            <!-- 任务引用选项卡 -->
            <bk-tabpanel name="instance" :title="i18n.instance">
                <div class="p20">{{i18n.instanceName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 原子选择器 -->
                                <el-col :span="5" class="analysis-atom-select">
                                    {{i18n.atom}}
                                    <el-select filterable :placeholder="i18n.choice" @change="dataTableDataByInstance()" v-model="atom">
                                        <el-option v-for="item in components" :key="item.code"  :label="item.group_name +'-'+ item.name" :value="item.code">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 原子选择器end -->

                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime2}}
                                    <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByInstance()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByInstance()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->

                                <!-- 分组选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.category}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByInstance()" v-model="category">
                                        <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                 <!-- 分组选择器end -->

                            </el-col>
                        </div>
                        <!-- 表格 -->
                        <el-col :span="22" :offset="1" class="analysis-dataTable">
                            <data-table-pagination
                            :data="instanceData"
                            :total="instanceTotal"
                            :otherHeight="otherHeight"
                            :pagination="instancePagination"
                            :options="dataTableOptions"
                            :columns="instanceColumns"
                            :operates="instanceOperates"
                            @handleSizeChange="instanceHandleSizeChange"
                            @handleIndexChange="instanceHandleIndexChange">
                            </data-table-pagination>
                        </el-col>
                        <!-- 表格end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 任务引用选项卡 end -->

        </bk-tab>
        <!-- 选项卡end -->
    </el-container>
    <!-- 内容end -->
</div>
</template>

<script>
import '@/utils/i18n.js'
import {mapActions, mapState} from 'vuex'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import NoData from '@/components/common/base/NoData.vue'
import {AnalysisMixins} from '@/assets/js/analysisMixins.js'

export default {
    name: 'AnalysisAtom',
    components: {
        NoData,
        DataTablePagination
    },
    mixins: [AnalysisMixins],
    data () {
        return {
            i18n: {
                startAndEndTime1: gettext('流程创建时间：'),
                startAndEndTime2: gettext('任务开始时间：'),
                choice: gettext('请选择'),
                category: gettext('分类：'),
                businessName: gettext('业务：'),
                cite: gettext('引用次数'),
                citeName: gettext('各原子被流程模板直接引用次数'),
                atom: gettext('原子：'),
                template: gettext('流程详情'),
                templateName: gettext('引用原子的流程模板列表'),
                instance: gettext('任务详情'),
                instanceName: gettext('执行原子的任务列表'),
                execute: gettext('执行耗时'),
                executeName: gettext('各原子执行统计数据')
            },
            appType: 'app',
            time: [0, 0],
            category: '',
            biz_cc_id: '',
            atom: '',
            templateData: [], // 模板数据
            otherHeight: 208, // 除了table表格之外的高度，为了做table表格的高度自适应
            templateTotal: 0, // table数据总条数,
            templatePageIndex: 1, // 当前页码
            templateLimit: 15, // 每页数量
            templatePagination: {
                // 分页操作
                limit: this.templateLimit,
                pageIndex: this.templatePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            templateColumns: [
                {
                    prop: 'businessName', // 识别id
                    label: gettext('所属业务'), // 表头显示名称
                    align: 'center',// 对其格式，可选（right，left，center）
                    minWidth: 150
                },
                {
                    prop: 'templateName', // 识别id
                    label: gettext('流程名称'), // 表头显示名称
                    align: 'center',
                    minWidth: 285
                },
                {
                    prop: 'editTime',
                    label: gettext('更新时间'),
                    align: 'center',
                    width: 300
                },
                {
                    prop: 'editor',
                    label: gettext('更新人'),
                    align: 'center',
                    formatter: (row, column, cellValue) => {
                        return `<span>${row.editor || '--'}</span>`
                    }
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center',
                    width: 115
                }
            ],
            templateOperates: {
                // width: 200, // 按钮宽度
                // fixed: 'right', // 按钮对齐方式
                isShow: true, // 是否显示操作按钮
                flex: '0 0 100%', // 按钮间距
                data: [
                    {
                        label: gettext('查看'), // 按钮显示文字
                        show: true, // 是否展示该按钮
                        cls: 'bk-button bk-primary btn-size-mini', // 按钮额外样式
                        method: (index, row) => {
                            // 回调方法
                            this.onTemplateHandleView(index, row)
                        }
                    }
                ]
            },
            executeData: [],
            executeTotal: 0,
            executePageIndex: 1,
            executeLimit: 15,
            executePagination: {
                // 分页操作
                limit: this.executeLimit,
                pageIndex: this.executePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            executeColumns: [
                {
                    prop: 'componentName',
                    label: gettext('原子名称'),
                    align: 'center',
                    minWidth: 200
                },
                {
                    prop: 'executeTimes',
                    label: gettext('执行次数'),
                    align: 'center',
                    width: 170
                },
                {
                    prop: 'failedTimes',
                    label: gettext('失败次数'),
                    align: 'center',
                    width: 160
                },
                {
                    prop: 'avgExecuteTime',
                    label: gettext('平均执行耗时（秒）'),
                    align: 'center',
                    width: 300
                },
                {
                    prop: 'failedTimesPercent',
                    label: gettext('失败率'),
                    align: 'center',
                    width: 140
                }
            ],
            executeOperates: {
                isShow: false // 不显示操作按钮
            },
            instanceData: [],
            instanceTotal: 0,
            instancePageIndex: 1,
            instanceLimit: 15,
            instancePagination: {
                limit: this.instanceLimit,
                pageIndex: this.instancePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            instanceColumns: [
                {
                    prop: 'businessName',
                    label: gettext('所属业务'),
                    align: 'center',
                    minWidth: 150
                },
                {
                    prop: 'instanceName',
                    label: gettext('任务名称'),
                    align: 'center',
                    minWidth: 285
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center',
                    width: 215
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center'
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center',
                    width: 115
                }
            ],
            instanceOperates: {
                width: 100,
                flex: '0 0 100%',
                isShow: true,
                data: [
                    {
                        label: gettext('查看'),
                        show: true,
                        cls: 'bk-button bk-primary btn-size-mini',
                        method: (index, row) => {
                            // 回调方法
                            this.onInstanceHandleView(index, row)
                        }
                    }
                ]
            },
            components: [],
            citeShow: true,
            noDataHeight: '600px',
            tabName: 'cite'
        }
    },
    created () {
        // 设置默认为30天前的查询
        let date = new Date()
        date.setHours(0, 0, 0)
        this.time[1] = Date.parse(date)
        date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
        this.time[0] = Date.parse(date)
    },
    mounted () {
        this.plotlyDataByCite()
        // 初始化未有数据时
        if (this.categorys.length === 0) {
            this.getCategorys()
        }
        if (this.bizList.length === 0) {
            this.getBizList()
        }
        if (this.components.length === 0) {
            this.getComponentList()
        }
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        })
    },
    methods: {
        ...mapActions('atomList/', [
            'queryAtomData',
            'loadSingleAtomList'
        ]),
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        // 切换每页显示的数量
        templateHandleSizeChange (limit) {
            this.templatePageIndex = 1
            this.templateLimit = limit
            // 根据 pageSize 和 limit 进行请求发送
            this.dataTableDataByTemplate()
        },
        // 切换页码
        templateHandleIndexChange (pageIndex) {
            this.templatePageIndex = pageIndex
            // 根据 pageSize 和 limit 进行请求发送
            this.dataTableDataByTemplate()
        },
        executeHandleSizeChange (limit) {
            this.executePageIndex = 1
            this.executeLimit = limit
            this.dataTableDataByExecute()
        },
        executeHandleIndexChange (pageIndex) {
            this.executePageIndex = pageIndex
            this.dataTableDataByExecute()
        },
        instanceHandleSizeChange (limit) {
            this.instancePageIndex = 1
            this.instanceLimit = limit
            this.dataTableDataByInstance()
        },
        instanceHandleIndexChange (pageIndex) {
            this.instancePageIndex = pageIndex
            this.dataTableDataByInstance()
        },
        // 查看信息
        onTemplateHandleView (index, row) {
            window.open(this.site_url + 'template/edit/' + row.businessId + '/?template_id=' + row.templateId)
        },
        onInstanceHandleView (index, row) {
            window.open(this.site_url + 'taskflow/execute/' + row.businessId + '/?instance_id=' + row.instanceId)
        },
        changeTabPanel (name) {
            this.tabName = name
            switch (name) {
                case 'cite' :
                    this.plotlyDataByCite()
                    break
                case 'template' :
                    this.dataTableDataByTemplate()
                    break
                case 'execute' :
                    this.dataTableDataByExecute()
                    break
                case 'instance' :
                    this.dataTableDataByInstance()
                    break
            }
        },
        async queryPlotlyData (data, id, title) {
            const templateData = await this.queryAtomData(data)
            let total = templateData.data.total
            if (id === 'citeBar' && total === 0) {
                this.citeShow = false
                return
            }
            else if (id === 'citeBar') {
                this.citeShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                    var myPlot = document.getElementById(id)
                    var vueThis = this
                    myPlot.on('plotly_click', function (data) {
                        let point = data.points[0]
                        let id = point.data.id[point.pointIndex]
                        vueThis.tabName = 'template'
                        vueThis.atom = id
                        vueThis.templatePageIndex = 1
                        vueThis.templateLimit = 15
                        vueThis.dataTableDataByTemplate()
                    })
                })
            }
        },
        plotlyDataByCite () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'atom_cite',
                conditions: JSON.stringify({
                    create_time: time[0],
                    biz_cc_id: this.biz_cc_id,
                    finish_time: time[1]
                })
            }
            this.queryPlotlyData(data, 'citeBar', gettext('原子被引用次数'))
        },
        async queryDataTableData (data) {
            const templateData = await this.queryAtomData(data)
            switch (data.group_by) {
                case 'atom_template':
                    this.templateData = templateData.data.groups
                    this.templateTotal = templateData.data.total
                    break
                case 'atom_execute':
                    this.executeData = templateData.data.groups
                    this.executeTotal = templateData.data.total
                    break
                case 'atom_instance':
                    this.instanceData = templateData.data.groups
                    this.instanceTotal = templateData.data.total
                    break
            }
        },
        dataTableDataByTemplate () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'atom_template',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category,
                    component_code: this.atom
                }),
                pageIndex: this.templatePageIndex,
                limit: this.templateLimit
            }
            this.queryDataTableData(data)
        },
        dataTableDataByExecute () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'atom_execute',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category
                }),
                pageIndex: this.executePageIndex,
                limit: this.executeLimit
            }
            this.queryDataTableData(data)
        },
        dataTableDataByInstance () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'atom_instance',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category,
                    component_code: this.atom
                }),
                pageIndex: this.instancePageIndex,
                limit: this.instanceLimit
            }
            this.queryDataTableData(data)
        },
        // 获取原子列表并设置为第一个
        async getComponentList () {
            this.components = await this.loadSingleAtomList()
            this.atom = this.components[0].code
        }
    }
}
</script>

